<script>
  import CommodityRack from "../../CommodityRack/index.vue"
  export default {
    name: "CommoditySales_new",
    data() {
      var mbgi_money_2 = (rule, value, callback) => {
        let reg = /^[0-9]*$/
        reg.test(value*1)
        if (!reg.test(value*1)) {
          callback(new Error('请输入商品优惠价'));
        } else {
          callback();
        }
      };
      var mbgi_money_3 = (rule, value, callback) => {
        let reg = /^[0-9]*$/
        reg.test(value*1)
        if (!reg.test(value*1)) {
          callback(new Error('请输入商品特惠价'));
        } else {
          callback();
        }
      };
      return {
        toExamineMsg:{
          tableData:[
            {key:'规格',value:'mbgi_goods_describer'},
            {key:'品牌',value:'mbgi_goods_marker'},
            {key:'原价',value:'mbgi_money_1'},
            {key:'优惠价',value:'mbgi_money_2'},
            {key:'库存',value:'mbug_count_num'},
          ]
        },
        str: '',
        showData: false,
        CommodityRackId: "",
        CommodityRackMsg: [],
        CommodityRackIndex: 0,
        CommodityRackData: {},
        ruleForm: {
          di_uuid: '',
        },
        di_uuid: '',
        cardBodyStyle: 'padding-top:10px;padding-bottom:10px;',
        tableData_equipment: [],//设备数据
        activeIndex: '1',
        key1: '',
        tableData: [],//表格数据
        sortOrderCol: '',
        /** 查询参数 */
        queryParam: {
          totalRow: 0,
          currentPage: 1,
          pageSize: this.customGlobal.pageSize,
        },
        commodityForm: {
          mbmi_mer_name: '',
          mbgi_money_1: '',
          mbgi_money_2: '',
          mbgi_money_3: '',
          mbgi_count_num: '',
          count_num: '',
          mbgi_goods_name: '',
          mbgi_goods_alias_name: '',
          mbgi_goods_marker: '',
          mbgi_goods_describer: '',
        },
        
        commodityFormrules: {
          mbgi_money_2: [
            { validator:mbgi_money_2, message: '请输入商品优惠价', trigger: 'blur' },
            { required: true, message: '请输入商品优惠价', trigger: 'blur' },
          ],
          mbgi_money_3: [
            { validator:mbgi_money_3, message: '请输入商品特惠价', trigger: 'blur' },
            { required: true, message: '请输入商品特惠价', trigger: 'blur' },
          ],
          count_num: [
            { pattern:/^[0-9]*$/, message: '请输入商品库存', trigger: 'blur' },
            { required: true, message: '请输入商品库存', trigger: 'blur' },
          ],
        },
        disabled2: false,//商品开关
        disabled1: false,
        tableData_commodity: [],
        inventory_quantity: '',
        dialogVisible: false,
        tableDialgTitle: '',
        disabled: false,
        mbgi_goods_img_path: '',
        tableData_shop: [],
        shop_id: '',
        examine_but: false,
        edit_but: false,
        within_but: false,
        examine_dialogVisible: false,
        examineForm: {
          bmi_mer_name: '',
          mbgi_goods_name: '',
          mbgi_goods_marker: '',
          mbgi_goods_describer: '',
          mbgi_source: '',
          bii_name: '',
          times: '',
          uuid: '',
          mbug_count_num: '',
          mbug_sale_mode: '',
          mbgi_money_1: '',
          mbgi_money_2: '',
          mbgi_goods_img_path: '',
          mbug_uuid: '',
          mbug_status: '',
        },
        mbug_status: '',
        mbug_describer: '',
        opinion_dialogVisible: false,
        examineForm1: {
          opinion: ''
        },
        mbgi_uuid: '',
        table_show1: false,
        table_show2: false,
        table_show3: false,
        mbug_show: '',
        update_type: '',
        associated_dialogVisible: false,
        associatedForm: {

        },
        associatedFormrules: {

        }
      }
    },
    components: { CommodityRack },
    filters: {
      capitalize: function (value) {
        if (value) {
          let arr = Array.from(new Set(value.split(',')))
          arr.forEach((res, i) => {
            if (res == '') {
              console.log(arr);
              arr.splice(i, 1)
            }
          })
          return arr.toString()
        }
      }
    },
    created() {
      this.key1 = '1';
      this.table_show1 = true;
      this.table_show2 = false;
      this.table_show3 = false;
      this.examine_but = true;
      this.edit_but = false;
      this.within_but = false;
      this.select_equipment();
    },
    methods: {
      CommodityRackClick() {
        console.log('------zhixing');
        this.CommodityRackMsg = []
        this.CommodityRackIndex = ''
      },
      //组件传值，获取勾选货道与输入库存
      checkedChange(data, i) {
        console.log(data, i, this.CommodityRackData);
        this.CommodityRackMsg = data
        if (i) {
          this.CommodityRackIndex = this.CommodityRackData.A - i
        } else {
          this.CommodityRackIndex = this.CommodityRackData.A
        }
      },
      /**
        * @description: 添加商品与货道库存绑定关系操作
        * @param:传入参数需要格式
        * @return: java.lang.String
        * @date: 2022/1/20 13:54
      */
      associated_submitForm() {
        console.log(this.CommodityRackMsg, "this.CommodityRackMsg");
        let countent = 0
        this.CommodityRackMsg.forEach(res => {
          return countent += res.value * 1
        });
        console.log(countent);
        if (countent > this.CommodityRackIndex) {
          return this.$message({
            message: '请输入正确的库存数，当前可用库存为' + this.CommodityRackIndex,
            type: 'warning'
          });
        }

        let that = this;
        let _jsondata_ = []
        if (this.CommodityRackMsg.length > 0) {
          _jsondata_ = this.CommodityRackMsg.map(res => {
            return {
              obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
              obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
              obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
              obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
              obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
              mdgi_mer_uuid: this.CommodityRackData.mdgi_mer_uuid,
              mdgi_goods_type_uuid: this.CommodityRackData.mdgi_goods_type_uuid,
              mdgi_number: res.id,
              kucun: res.value,
              mdgi_device_uuid: this.di_uuid == '*' ? this.CommodityRackId : this.di_uuid,
            }
          });
        } else {
          this.associated_dialogVisible = false
            this.tableDialgTitle = '';
          // this.$message({
          //   message: '请先勾选货道并填写库存',
          //   type: 'warning'
          // });
          return
        }
        let _jsondata_show = _jsondata_.every(res => {
          return /(^[1-9]\d*$)/.test(res.kucun)
        })
        if (!_jsondata_show) {
          this.$message({
            message: '请输入正确的库存数',
            type: 'warning'
          });
          return
        }
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0)'
        });
        let post = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.Binding_of_goods_and_cargo_lanes_add,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        post.then((res) => {
          this.select_in_equipment()
          if (res.data.status == 'success') {
            this.associated_dialogVisible = false
            this.tableDialgTitle = '';
            this.$message({
              message: '绑定成功',
              type: 'success'
            });
            // this.$refs.child.sing()
            loading.close();
          } else {
            // that.notApproved.tableMsg = {};
            console.info("货道列表查询失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          console.log("异步调用出错:" + JSON.stringify(error));
          loading.close();
          // that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // that.notApproved.tableMsg = {};
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '货道列表查询失败!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      //下拉框点击事件
      equipment_Selection(key) {
        this.di_uuid = key;
        this.ruleForm.di_uuid = key;
        if (this.key1 === '1') {
          this.table_show1 = true;
          this.table_show2 = false;
          this.table_show3 = false;
          this.queryParam.currentPage = 1;
          this.select_to_examine();
        } else if (this.key1 === '2') {
          this.table_show1 = false;
          this.table_show2 = true;
          this.table_show3 = false;
          this.queryParam.totalRow = 0;
          this.tableData = [];
          this.queryParam.currentPage = 1;
          this.select_flag_equipment();
        } else if (this.key1 === '3') {
          this.table_show1 = false;
          this.table_show2 = false;
          this.table_show3 = true;
          this.queryParam.totalRow = 0;
          this.tableData = [];
          this.queryParam.currentPage = 1;
          this.select_in_equipment();
        }
      },
      handleSelect(key, keyPath) {
        console.log('上：' + key, keyPath);
        this.key1 = key;
        if (this.key1 === '1') {
          this.table_show1 = true;
          this.table_show2 = false;
          this.table_show3 = false;
          this.examine_but = true;
          this.edit_but = false;
          this.within_but = false;
          this.queryParam.currentPage = 1;
          this.select_to_examine();
        } else if (this.key1 === '2') {
          this.table_show1 = false;
          this.table_show2 = true;
          this.table_show3 = false;
          this.examine_but = false;
          this.edit_but = true;
          this.within_but = false;
          this.tableData = [];
          this.queryParam.currentPage = 1;
          this.select_flag_equipment();
        } else if (this.key1 === '3') {
          this.table_show1 = false;
          this.table_show2 = false;
          this.table_show3 = true;
          this.examine_but = false;
          this.edit_but = false;
          this.within_but = true;
          this.tableData = [];
          this.queryParam.currentPage = 1;
          this.select_in_equipment();
        }
        // if(this.key === '1'){
        //   this.mbmi_status = '2';
        // }else if (this.key === '2'){
        //   this.mbmi_status = '1';
        // }
        // this.queryParam.currentPage = 1;
        // this.select_table();
      },
      /**  表格列排序设置   */
      sortColumn(column) {
        //  console.log("已经排序项：" + JSON.stringify(column));
        let orderFlag = '';
        if (column.order != null) {
          if (column.order === "descending") {
            orderFlag = 'desc';
          } else if (column.order === "ascending") {
            orderFlag = 'asc';
          }
          //  console.info("属性名称："+ column.prop+",排序方式："+column.order);
          this.sortOrderCol = column.prop + " " + orderFlag;
          //  console.info("用户选择排序列："+this.sortOrderCol);

          if (this.key1 === '1') {
            this.queryParam.currentPage = 1;
            this.select_to_examine();
          } else if (this.key1 === '2') {
            this.queryParam.currentPage = 1;
            this.select_flag_equipment();
          } else if (this.key1 === '3') {
            this.queryParam.currentPage = 1;
            this.select_in_equipment();
          }
        } else {
          this.sortOrderCol = '';
