          uuid_type = '网点内';
        } else if (row.di_address_type === '2') {
          uuid_type = '网点外';
        }
        this.examineForm.uuid = uuid_type + '：' + row.mbug_device_uuid.substring(row.mbug_device_uuid.length - 9);
        this.examineForm.times = row.mbgi_up_starttime + '至' + row.mbgi_up_endtime;
        this.examineForm.mbug_count_num = row.mbug_count_num;
        if (row.mbug_sale_mode === '1') {
          this.examineForm.mbug_sale_mode = '机器交付';
        } else if (row.mbug_sale_mode === '2') {
          this.examineForm.mbug_sale_mode = '网点交付';
