              that.queryParam.currentPage = 1;
              that.select_flag_equipment();
            } else if (that.key1 === '3') {
              that.queryParam.currentPage = 1;
              that.select_in_equipment();
            }
          } else {
            console.info("商品移至设备内失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商品移至设备内出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商品移至设备内出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      move_out_of_the_device(index, row) {
        if (row.track_number) {
          let arr = Array.from(new Set(row.track_number.split(',')))
          arr.forEach((res, i) => {
            if (res == '') {
              console.log(arr);
              arr.splice(i, 1)
            }
          })
          if (arr.toString()) {
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">请先取消货道关联</span>'
            });
            return
          }
        }

        let that = this;
        let _jsondata_ = {
          mbgi_sale_mode: '2',
          obj_bank_center_id: that.customGlobal.userInfoExt.cui_bank_center_id,
          obj_province_id: that.customGlobal.userInfoExt.cui_province_id,
          obj_city_id: that.customGlobal.userInfoExt.cui_city_id,
          obj_county_id: that.customGlobal.userInfoExt.cui_county_id,
          obj_station_id: that.customGlobal.userInfoExt.cui_station_id,
          mdgi_device_uuid: row.mbgd_device_uuid,
          mdgi_mer_uuid: row.mbgi_mer_uuid,
          mdgi_goods_type_uuid: row.mbgi_uuid
        };
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, 0)'
        });
        var a1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.From_inside_to_outside,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          { "Content-Type": "application/x-www-form-urlencoded" },
          this.qs.stringify({ jsondata: JSON.stringify(_jsondata_) })
        );
        a1.then((res) => {
          if (res.data.status == 'success') {
            loading.close();
            this.$message({
              type: 'success',
              showClose: true,
              duration: 3000,
              dangerouslyUseHTMLString: true,
              message: '<span style="color:black;">已移动至设备外，不要忘记将设备内的商品取出</span>'
            });
            if (that.key1 === '1') {
              that.queryParam.currentPage = 1;
              that.select_to_examine();
            } else if (that.key1 === '2') {
              that.queryParam.currentPage = 1;
              that.select_flag_equipment();
            } else if (that.key1 === '3') {
              that.queryParam.currentPage = 1;
              that.select_in_equipment();
            }
          } else {
            console.info("商品移至设备外失败");
            loading.close();
            if (res.data.msg === '请登录认证') {
              this.$router.push({ path: '/login' });
            }
          }
        }, error => {
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(error.message), '商品移至设备外出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:" + JSON.stringify(res));
          loading.close();
          // this.companyDialogFormVisible =false;//保存失败关闭dialog
          this.$alert(JSON.stringify(res.message), '商品移至设备外出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      },
      //修改
      update_commodity1(index, row) {
        this.dialogVisible = true;
        this.commodityForm.mbgi_goods_name = row.mbgi_goods_name;
        this.commodityForm.mbgi_money_1 = row.mbgi_money_1;
        this.commodityForm.mbgi_money_2 = row.mbgi_money_2;
        this.commodityForm.mbgi_money_3 = row.mbgi_money_3;
        this.commodityForm.mbgi_count_num = row.remaining_inventory;
        this.commodityForm.count_num = row.count_num;
        this.mbgi_goods_img_path = row.mbgi_goods_img_path;
        this.commodityForm.mbgi_goods_alias_name = row.mbgi_goods_alias_name;
        this.commodityForm.mbgi_goods_marker = row.mbgi_goods_marker;
        this.commodityForm.mbgi_goods_describer = row.mbgi_goods_describer;
        this.disabled = false;
        this.disabled1 = true;
        this.disabled2 = true;
        this.inventory_quantity = row.count_num;
        this.shop_id = row.mbgi_mer_uuid;
        this.mbgi_uuid = row.mdgi_goods_type_uuid;
        this.di_uuid = row.mbgd_device_uuid;
        console.log(this.shop_id);
        this.update_type = '3';
      },
      Associated_cargo_way(index, row) {
        console.log(this.di_uuid);
        this.CommodityRackData = row
        this.CommodityRackId = row.mbgd_device_uuid
        this.associated_dialogVisible = true;
        this.tableDialgTitle = '关联货道';
      },
      associated_cancelForm() {
        this.associated_dialogVisible = false;
        this.tableDialgTitle = '';
        this.CommodityRackMsg = []
        this.select_in_equipment()
        this.tableDialgTitle = '';

      },
      // associated_submitForm() {
      //   this.associated_dialogVisible = false;
      // }
    }
  }
</script>

<style scoped>
  .account-plat-formitem /deep/ .el-form-item {
    margin-bottom: 25px !important;
  }
  /* .commoditySales /deep/ .el-form-item__error {
    left: 34px !important;
  } */

  .account-plat-card1 /deep/ .el-card__header {
    /*background:#f5f5f5 !important;*/
    background: #f2f3f8 !important;
    /*height: 10px!important;*/
    padding: 10px !important;
  }

  .account-plat-card2 /deep/ .el-card__body {
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    padding-left: 20px !important;
    padding-right: 20px !important;
  }

  .account-plat-fixed-right /deep/ .el-table__fixed-right {
    height: 100% !important;
  }

  .account-plat-fixed /deep/ .el-table__fixed {
    height: 100% !important;
  }
</style>


